import React, { Component, lazy, Suspense } from "react";
import { connect } from "react-redux";
import Axios from "axios";
import { Link } from "react-router-dom";
import {
  setTampungCiok,
  settampungRongsok,
} from "../actions/pembayaranSupplierAction";
import Swal from "sweetalert2";
import { getUUID } from "../actions/fakturAction";
import PrintNota from "../Stocking/Component/PrintNota";
import { warning } from "../Alert";
import Skeleton from "react-loading-skeleton";
import { finishSend, progressSend } from "../actions/LogicAction";
import HeaderCard from "../HeaderCard";

const PembayaranSupplierSimpan = lazy(() =>
  import("./PembayaranSupplierSimpan")
);
const maptoState = (state) => {
  return {
    pembayaranSupplierList: state.provinsi.pembayaranSupplier,
    tampungCiok: state.provinsi.tampungCiok,
    tampungRongsok: state.provinsi.tampungRongsok,
    totalCiok: state.provinsi.totalCiok,
    totalRongsok: state.provinsi.totalRongsok,
    totalBrutoCiok: state.provinsi.totalBrutoCiok,
    totalBrutoRongsok: state.provinsi.totalBrutoRongsok,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    onSend: state.provinsi.onSend,
  };
};
const server = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class PembayaranSupplier extends Component {
  constructor(props) {
    super(props);
    var date = new Date();
    var formatedDate = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    this.state = {
      listSupplier: [],
      tanggal: formatedDate,
      bonBayar: "",
      detail: "",
      tipeTransaksi: "BARANG",
    };
  }
  componentDidMount() {
    Axios.get(server + "suppliers")
      .then((res) =>
        this.setState({
          listSupplier: res.data,
        })
      )
      .catch((err) => console.log(err));
    this.props.dispatch(setTampungCiok());
    this.props.dispatch(settampungRongsok());
    this.props.dispatch(getUUID());
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  handleSubmit(data) {
    if (data.noBon === undefined) {
      warning("Mohon isi nomor bon");
      return false;
    } else if (data.tanggal === undefined) {
      warning("Mohon isi Tanggal");
      return false;
    } else if (data.supplier === undefined) {
      warning("Mohon isi Supplier");
      return false;
    } else if (
      localStorage.getItem("alatBayarBarang") === null &&
      localStorage.getItem("alatBayarTrf") === null &&
      localStorage.getItem("alatBayarTunai") === null &&
      localStorage.getItem("alatBayarTunaiRp") === null &&
      localStorage.getItem("alatBayarTrfRp") === null
    ) {
      warning("Mohon Masukan Alat Pembayaran Paling Tidak 1 Jenis");
      return false;
    }
    var bayarBarang =
      JSON.parse(localStorage.getItem("alatBayarBarang")) || null;
    var bayarTrf =
      JSON.parse(localStorage.getItem("alatBayarTrf")) === null
        ? JSON.parse(localStorage.getItem("alatBayarTrf")) || null
        : null;
    var bayarTunai =
      JSON.parse(localStorage.getItem("alatBayarTunai")) === null
        ? JSON.parse(localStorage.getItem("alatBayarTunaiRp")) || null
        : null;
    var split = data.supplier ? data.supplier : "DEFAULT | DEFUALT";
    var supplier = split.split("|");
    var isi = {
      alatBayarBarang: bayarBarang,
      alatBayarTunai: bayarTunai,
      alatBayarTrf: bayarTrf,
      alatBayarGiro: null,
      totalFakutrTrx: null,
      keterangan: "-",
      totalAlatBayar: {
        totalRsk: this.props.totalRongsok,
        totalCt: this.props.totalCiok,
        totalTunaiRp: bayarTunai !== null ? bayarTunai[0].tunaiNetto : 0,
        totalTrfRp: bayarTrf !== null ? bayarTrf[0].transferNetto : 0,
        totalPutusGr: 0,
        tipeData: "BON GR",
        totalPutusRp: 0,
      },
      kodeSupplier: supplier[0],
      namaSupplier: supplier[1],
      noBon: data.noBon,
      noFaktur: this.props.noFaktur,
      tanggal: data.tanggal,
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    localStorage.setItem("notaPembayaranSupplier", JSON.stringify(isi));
    this.props.dispatch(progressSend());
    // INISIALISASI AUTOTABLE
    const tableRows = [
      [
        "Tunai",
        "Tunai",
        parseFloat(
          bayarTunai !== null ? bayarTunai[0].tunaiRp : 0
        ).toLocaleString("id-ID"),
        parseFloat(
          bayarTunai !== null ? bayarTunai[0].tunaiKurs : 0
        ).toLocaleString("id-ID"),
        "",
        parseFloat(bayarTunai !== null ? bayarTunai[0].tunaiNetto : 0),
        "",
        "",
      ],
      [
        "Transfer",
        "Transfer",
        parseFloat(
          bayarTrf !== null ? bayarTrf[0].transferRp : 0
        ).toLocaleString("id-ID"),
        parseFloat(
          bayarTrf !== null ? bayarTrf[0].transferKurs : 0
        ).toLocaleString("id-ID"),
        "",
        parseFloat(bayarTrf !== null ? bayarTrf[0].transferNetto : 0),
        "",
        "",
      ],
    ];
    var ciok = JSON.parse(localStorage.getItem("ciokTemp")) || [];
    ciok.forEach((ticket, i) => {
      const ticketData = [
        "Ciok",
        ticket.kodeBarang,
        ticket.bruto,
        "",
        ticket.kadarTransaksi,
        ticket.netto,
        "",
        "",
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    var rongsok = JSON.parse(localStorage.getItem("rongsokTemp")) || [];
    rongsok.forEach((ticket, i) => {
      const ticketData = [
        "Rongsok",
        ticket.kodeBarang,
        ticket.bruto,
        "",
        ticket.kadarTransaksi,
        ticket.netto,
        "",
        "",
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    var discount = [
      "DISCOUNT",
      "DISCOUNT",
      localStorage.getItem("discount") !== null
        ? localStorage.getItem("discount")
        : 0,
      "",
      "",
      localStorage.getItem("discount") !== null
        ? localStorage.getItem("discount")
        : 0,
      "",
      "",
    ];
    tableRows.push(discount);
    var bonus = [
      "BONUS",
      "BONUS",
      localStorage.getItem("bonus") !== null
        ? localStorage.getItem("bonus")
        : 0,
      "",
      "",
      localStorage.getItem("bonus") !== null
        ? localStorage.getItem("bonus")
        : 0,
      "",
      "",
    ];
    tableRows.push(bonus);
    var denda = [
      "DENDA",
      "DENDA",
      localStorage.getItem("denda") !== null
        ? localStorage.getItem("denda")
        : 0,
      "",
      "",
      localStorage.getItem("denda") !== null
        ? localStorage.getItem("denda")
        : 0,
      "",
      "",
    ];
    tableRows.push(denda);
    console.log(JSON.stringify(tableRows));
    var columTable = [
      "Jenis \nPembayaran",
      "Nama \nBarang",
      "Gross \nBayar",
      "Harga",
      "Kadar",
      "Netto",
      "Pkg",
      "Gw+Pkg",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    Axios.post(server + "/spp/dc/byr", isi)
      .then((res) =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => this.props.dispatch(finishSend()))
          .then(() => localStorage.clear())
          .then(() => localStorage.setItem("isLogin", true))
          .then(() => this.props.dispatch(getUUID()))
          .then(() => this.props.history.push("KlasifikasiSupplier"))
          .then(() =>
            PrintNota(
              "Tanggal",
              data.tanggal,
              "",
              "",
              "Nomor \nPembayaran",
              data.noBon,
              "",
              "",
              "ADMIN",
              data.tanggal,
              "",
              columTable,
              "NOTA PEMBAYARAN SUPPLIER",
              tableRows
            )
          )
          .then(() => window.history.back())
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: err,
          showConfirmButton: false,
          timer: 1500,
        })
      );
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true">×</span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <ol className="breadcrumb mb-2">
          <Link to="/KlasifikasiSupplier">
            <button type="button" className="btn btn-secondary">
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Pembayaran Hutang Supplier" />
              <div className="card-body">
                <div className="row">
                  <div className="col-lg-12">
                    <ul className="nav nav-pills bg-nav-pills nav-justified mb-3">
                      <li className="nav-item">
                        <a
                          href="#rupiah"
                          data-toggle="tab"
                          aria-expanded="false"
                          className="nav-link rounded-0 active"
                          onClick={() =>
                            this.setState({
                              tipeTransaksi: "BARANG",
                            })
                          }
                        >
                          <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
                          <span className="d-none d-lg-block">Gram</span>
                        </a>
                      </li>
                      <li className="nav-item">
                        <a
                          href="#gram"
                          data-toggle="tab"
                          aria-expanded="true"
                          className="nav-link rounded-0 "
                          onClick={() =>
                            this.setState({
                              tipeTransaksi: "RUPIAH",
                            })
                          }
                        >
                          <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
                          <span className="d-none d-lg-block">Rupiah</span>
                        </a>
                      </li>
                    </ul>
                    <div className="tab-content">
                      <div className="tab-pane show active" id="rupiah">
                        <Suspense
                          fallback={<Skeleton width={"100%"} height={200} />}
                        >
                          <PembayaranSupplierSimpan
                            tampungCiok={this.props.tampungCiok}
                            tampungRongsok={this.props.tampungRongsok}
                            totalCiok={this.props.totalCiok}
                            totalRongsok={this.props.totalRongsok}
                            totalBrutoCiok={this.props.totalBrutoCiok}
                            totalBrutoRongsok={this.props.totalBrutoRongsok}
                            onSubmit={(data) => this.handleSubmit(data)}
                            jenisTransaksi={this.state.tipeTransaksi}
                            onSend={this.props.onSend}
                          />
                        </Suspense>
                      </div>
                      <div className="tab-pane" id="gram">
                        <Suspense
                          fallback={<Skeleton width={"100%"} height={200} />}
                        >
                          <PembayaranSupplierSimpan
                            tampungCiok={this.props.tampungCiok}
                            tampungRongsok={this.props.tampungRongsok}
                            totalCiok={this.props.totalCiok}
                            totalRongsok={this.props.totalRongsok}
                            totalBrutoCiok={this.props.totalBrutoCiok}
                            totalBrutoRongsok={this.props.totalBrutoRongsok}
                            onSubmit={(data) => this.handleSubmit(data)}
                            jenisTransaksi={this.state.tipeTransaksi}
                            onSend={this.props.onSend}
                          />
                        </Suspense>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(PembayaranSupplier);
